.loader{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    background-color: #060606;
    position: absolute;
    z-index: 10000;
}

@-webkit-keyframes spin{
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin{
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.loader_red{
border: 3px solid #d8123a;
position: absolute;
margin: auto;
top: 0;
bottom: 0;
right: 0;
left: 0;
width: 80px;
height: 80px;
border-radius: 50%;
/* Halve the circle */
border-bottom-color: transparent;
border-left-color: transparent;
/* Rotate the circle */
-webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
-webkit-animation: spin 1.5s infinite linear;
        animation: spin 1.5s infinite linear;

}
.loader_blue{
    border: 3px solid #34DDDD;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    /* Halve the circle */
    border-top-color: transparent;
    border-left-color: transparent;
    /* Rotate the circle */
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-animation: spin 1.5s infinite linear;
            animation: spin 1.5s infinite linear;

}
.loader_black{
    border: 3px solid #323030;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    /* Halve the circle */
    border-bottom-color: transparent;
    border-left-color: transparent;
    /* Rotate the circle */
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-animation: spin 1.5s infinite linear;
            animation: spin 1.5s infinite linear;

}
